#upload .dropzone{
    width:300px;
    height: 300px;
    border:2px dashed rgb(14, 201, 248);
    color:rgb(14, 201, 248);
    line-height: 300px;
    text-align: center;
    display: inline-block;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}
#upload #images img{
    width:150px;
}
#upload #images h6{
    font-size:12px;
    text-overflow: ellipsis;
    width:100%;
    overflow: hidden;
}
#upload #images{
    margin:20px;
    float:left;
    overflow-y:hidden;
    overflow-x:scroll;
    white-space: nowrap;
    max-width: 100%; 
    height:200px;
}
#upload #images .imgDiv{
    width:150px;
    display:inline-block;
    margin:20px;
}
#upload .dragover{
    color:rgb(14, 201, 248);
    border:2px solid rgb(14, 201, 248);
    font-size: 200%;
}
#upload #uploadButton{
    margin-top:20px;
    padding:5px;
    background:white;
    border:1px solid rgb(14, 201, 248);
    color:rgb(14, 201, 248);
    outline: none;
    padding-left:20px;
    padding-right:20px;
    -webkit-transition:all 0.5s;
    transition:all 0.5s;
}
#upload #uploadButton:hover{
    background:rgb(14, 201, 248);
    border: 1px solid rgb(14, 201, 248);
    color:#fff;
}
#upload .removeB{
    display:none;
}
#upload .imgDiv:hover .removeB{
    display:inline-block
}








































































































